import React from 'react';
import {
	Input,
	CustomInput
} from 'reactstrap';

import Datepicker from '../../../core/components/shared/Datepicker';

const Form=(props)=>{
	return(
		<form>
		    <div className="form-group avatar-box d-flex align-items-center">
		    	<img src="../assets/content/anonymous-400.jpg" width="40" height="40" alt="" className="rounded-500 mr-4" /> 
		    	<div className="custom-file">
		    	  <input type="file" className="custom-file-input" id="customFile" />
		    	  <label className="custom-file-label" for="customFile">Upload picture</label>
		    	</div>
		    </div>
		    <div className="form-group">
		    	<div className='row'>
		    		<div className='col-12 col-lg-4'>
		    			<input className="form-control" type="text" placeholder="Name" />
		    		</div>
		    		<div className='col-12 col-lg-4'>
		    			<input className="form-control" type="text" placeholder="Lastname" />
		    		</div>
		    		<div className='col-12 col-lg-4'>
		    			<input className="form-control" type="text" placeholder="Middlename" />
		    		</div>
		    	</div>
		    </div>
		    <div className="form-group">
		    	<div className='row'>
		    		<div className='col-12 col-lg-2'>
		    			<input className="form-control" type="number" placeholder="ID" />
		    		</div>
		    		<div className='col-12 col-lg-2'>
		    			<Input type='select'>
		    				<option selected disabled className='d-none'>Gender</option>
		    				<option>Male</option>
		    				<option>Female</option>
		    			</Input>
		    		</div>
		    		<div className='col-12 col-lg-4'>
		    			<Datepicker />
		    		</div>
		    	</div>
		    </div>
		    <div className="row">
		        <div className="col-12 col-sm-6">
		            <div className="form-group"><input className="form-control" type="number" placeholder="Age" /></div>
		        </div>
		        <div className="col-12 col-sm-6">
		            <div className="form-group">
		                
		            </div>
		        </div>
		    </div>
		    <div className="form-group mb-0"><textarea className="form-control" placeholder="Address" rows="3"></textarea></div>
		</form>
	);
}

export default Form;