const MyTable=({columnNames, rows, handleDelete, path, loading})=>{
	const [selectedItems, setSelectedItems] = useState([]);
	let tableRows=[];
	let tableCols=[];

	const handleSelectOne = (event, id) => {
  	  	const selectedIndex = selectedItems.indexOf(id);
  	  	let newSelectedItems = [];

  	  	if (selectedIndex === -1) {
  	    	newSelectedItems = newSelectedItems.concat(selectedItems, id);
  	  	} else if (selectedIndex === 0) {
  	    	newSelectedItems = newSelectedItems.concat(selectedItems.slice(1));
  	  	} else if (selectedIndex === selectedItems.length - 1) {
  	    	newSelectedItems = newSelectedItems.concat(selectedItems.slice(0, -1));
  	  	} else if (selectedIndex > 0) {
  	    	newSelectedItems = newSelectedItems.concat(
	  	      	selectedItems.slice(0, selectedIndex),
	  	      	selectedItems.slice(selectedIndex + 1)
  	    	);
  		}

  	  	setSelectedItems(newSelectedItems);
  	};

  	const handleSelectAll = event => {
  		var items=rows;
  	  	let selectedItems;
  	  	if (event.target.checked) {
  	    	selectedItems = items.map(i => i._id);
  	  	} else {
  	    	selectedItems = [];
  	  	}
  	  	setSelectedItems(selectedItems);
  	};

	if(!rows || loading){
		tableRows = (
			<tr>
				<td colSpan={columnNames.length + 2} className={'text-center py-4'} >
					<span>Loading...</span>
				</td>
			</tr>
		);
	}else{
		tableRows=rows.map((row,i)=>{
			return(
				<Row 
					key={i} 
					row={row} 
					path={path} 
					handleDelete={handleDelete}
				/>
			);
		});
	}

	// prepare columns
	// tableCols.push(
 	//      	<TableCell key={-1}>
 	//      	  	<Checkbox
	//       	    checked={selectedItems.length === rows.length}
	//       	    color={"primary"}
	//       	    indeterminate={
	//       	      selectedItems.length > 0 &&
	//       	      selectedItems.length < rows.length
	//       	    }
	//       	    onChange={handleSelectAll}
 	//      	  	/>
 	//      	</TableCell>
	// );
	columnNames.map((col, i)=>{
		tableCols.push(
			<th scope="col" key={i} >{col}</th>
		);
	});

	return(
		<table class="table table-hover">
		    <thead>
		    	<tr className='bg-primary text-white'>
		        	{tableCols}
		        </tr>
		    </thead>
		    <tbody>
		    	{tableRows}
		    </tbody>
		</table>
	);
}

const Pagination = (props)=>{
	return(
		<nav class="mt-4">
		    <ul class="pagination">
		        <li class="page-item disabled"><a class="page-link" href="#" aria-label="Previous" tabindex="-1" aria-disabled="true"><span class="icofont-simple-left"></span></a></li>
		        <li class="page-item active" aria-current="page"><a class="page-link" href="#">1 </a></li>
		        <li class="page-item"><a class="page-link" href="#">2 </a></li>
		        <li class="page-item"><a class="page-link" href="#">3 </a></li>
		        <li class="page-item"><a class="page-link" href="#" aria-label="Next"><span class="icofont-simple-right"></span></a></li>
		    </ul>
		</nav>
	);
}

const Row=({row, path, handleDelete})=>{
	let columns = row.columns.map((r, ind)=>{
		let cellStyle={}
		if(r.width){ cellStyle.width = r.width; }
		if(r.type && r.type === 'image'){
			return(
				<td key={ind} style={cellStyle}>
					<img src={r.url} className={r.className}/>
				</td>
			);
		}

		if(r.type === 'component'){
			let Cell = r.component;

			return(
				<td key={ind}>
					{Cell}
				</td>
			);
		}

		if(r.type === 'actions'){
			return(
				<td>
				    <div class="actions">
				    	<Link to={path + '/view'} class={"btn btn-dark btn-sm btn-square rounded-pill"}>
				    		<span class="btn-icon icofont-external-link"></span> 
				    	</Link>
				    	<Link to={path + '/edit/' + row._id} class={"btn btn-info btn-sm btn-square rounded-pill"}>
				    		<span class="btn-icon icofont-ui-edit"></span>
				    	</Link> 
				    	<button class="btn btn-error btn-sm btn-square rounded-pill" onClick={(e)=>handleDelete(e, row.id)}>
				    		<span class="btn-icon icofont-ui-delete"></span>
				    	</button>
				    </div>
				</td>
			);
		}
		return(
			<td>
			    <div className={r.className ? r.className : ''}>{r.text} </div>
			</td>
		);
	});

	return(
		<tr>
			{columns}
		</tr>
	);
}

const TableList = (props)=>{
	const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
	const [deleteId, setDeleteId] = useState(null);

	// handler when delete button is clicked
	const handleDelete = (event, deleteId)=>{
		setDeleteDialogOpen(true);
		setDeleteId(deleteId);
	}

	//handler when deletion is confirmed on dialog
	const onClose = (action)=>{
		setDeleteDialogOpen(false);
		if(action==='ok' && props.onDelete){
			props.onDelete(deleteId);
		}
	}



	return(
		<Fragment>
			<TableCard {...props} handleDelete={handleDelete} />
		</Fragment>
	)
}

export default TableList;