import React, {useState, lazy, Suspense} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {Route, Link} from 'react-router-dom';

import Table from '../../../core/components/shared/Table';

import {LOAD_ITEMS_REQ, LOAD_ITEMS_RES} from '../index';

class List extends React.Component{
	constructor(props){
		super(props);
	}
	componentDidMount(){
		const {items, loadItems, shouldLoadItems}=this.props;
		if(shouldLoadItems){
			loadItems();
		}
	}
	render(){
		console.log('Articles List',this.props);
		const {items, loadingItems}=this.props;

		if(loadingItems){
			return(
				<div>Loading...</div>
			)
		}

		const columnNames=['Photo', 'Name', 'ID', 'Age', 'Address', 'Number', 'Last visit', 'Actions'];

		const rows=items.map((p, i)=>{
			let Phone = (
				<div class="d-flex align-items-center nowrap text-primary">
					<span class="icofont-ui-cell-phone p-0 mr-2"></span>
					{p.phone}
				</div>
			);
			return {
				id: p.id,
				columns:[{
					type: 'image',
					url: '../assets/content/user-40-1.jpg',
					width: '40px',
					className:'rounded-500'
				},{
					className: 'strong',
					text: (p.lastname + ' ' + p.firstname).trim()
				},{
					className: 'text-muted',
					text: '10021'
				},{
					className: 'text-muted text-nowrap',
					text: '42'
				},{
					className: 'address-col',
					text: p.address
				},{
					type: 'component',
					component: Phone
				},{
					className: 'text-muted text-nowrap',
					text: '18 Dec 2019'
				},{
					type: 'actions'
				}]
			}
		});

		return(
			<div class="page-content">
			    <Table
            		loading={false}
            		columnNames={columnNames}
            		rows={rows}
            		path={'/patient'}
            		onDelete={(id)=>{console.log('deleting', id);}}
            	/>
			    <div class="add-action-box">
			    	<button class="btn btn-primary btn-lg btn-square rounded-pill" data-toggle="modal" data-target="#add-patient">
			    		<span class="btn-icon icofont-plus"></span>
			    	</button>
			    </div>
			</div>
		);
	}
}



List.propTypes={
	name:PropTypes.string
}

const mapStateToProps=(state)=>{
	const s=state.Patient;
	return {
		items: s.items,
		loadingItems: s.loadingItems,
		shouldLoadItems: s.shouldLoadItems
	}
}

const mapDispatchToProps=(dispatch)=>{
	return {
		loadItems:()=>{
			dispatch({
				type:LOAD_ITEMS_REQ
			});
			return fetch('/api/patients').then((res)=>{
				if(res.ok) return res.json();
				return [];
			}).then((items)=>{
				dispatch({
					type:LOAD_ITEMS_RES,
					payload:{
						items
					}
				});
			});
		}
	}
}

export const ListContainer=connect(
	mapStateToProps,
	mapDispatchToProps
)(List);

export default ListContainer;